import React, { forwardRef, useCallback } from 'react';
import PropTypes from 'prop-types';
import Modal from './../Modal';
import Fade from './../Fade';
import Paper from './../Paper';
import combine from './../utils/combine';
import { getHeight, getWidth, getSpacing, useStyles } from '../system';
import {
	easingPropType,
	durationPropType,
	stylesPropType,
} from './../utils/propTypes';

function getScrollStyles({ scroll }) {
	switch (scroll) {
		case 'body':
			return {
				container: {
					overflowY: 'auto',
					overflowX: 'hidden',
				},
				paper: {
					margin: '48px auto',
				},
			};

		default:
			// 'paper'
			return {
				container: {
					display: 'flex',
					justifyContent: 'center',
					alignItems: 'center',
				},
				paper: {
					flex: '0 1 auto',
					maxHeight: 'calc(100% - 96px)',
				},
			};
	}
}

function getSizingStyles(props) {
	const {
		theme: { breakpoints },
		fullScreen,
		fullWidth,
		maxWidth,
	} = props;
	const container = fullScreen
		? {
				...getHeight({ h: 1 }),
				...getWidth({ w: 1 }),
		  }
		: {};
	const paper = fullScreen
		? {
				borderRadius: '0px',
				...getHeight({ h: 1, hMax: 1 }),
				...getWidth({ w: 1, wMax: 1 }),
		  }
		: { borderRadius: '0px' };
	const w = fullWidth ? 1 : null;

	if (fullScreen) {
		return {
			container,
			paper,
		};
	}

	switch (maxWidth) {
		case 'xs':
			return {
				container,
				paper: {
					...paper,
					...getWidth({ w, wMax: breakpoints[0] }),
				},
			};
		case 'sm':
			return {
				container,
				paper: {
					...paper,
					...getWidth({ w, wMax: breakpoints[1] }),
					...getSpacing({ m: [null, 5.5] }),
				},
			};
		case 'md':
			return {
				container,
				paper: {
					...paper,
					...getWidth({ w, wMax: breakpoints[3] }),
					...getSpacing({ m: [null, null, 5.5] }),
				},
			};
		case 'lg':
			return {
				container,
				paper: {
					...paper,
					...getWidth({ w, wMax: breakpoints[4] }),
					...getSpacing({ m: [null, null, null, 5.5] }),
				},
			};
		default:
			return {
				container,
				paper: {
					...paper,
					...getSpacing({ m: 5.5 }),
				},
			};
	}
}

const getStyles = combine(getScrollStyles, getSizingStyles);
getStyles.propTypes = {
	// If `true`, the dialog will be full-screen
	fullScreen: PropTypes.bool,
	// If `true`, the dialog stretches to `maxWidth`.
	fullWidth: PropTypes.bool,
	/**
	 * Determine the max width of the dialog.
	 * The dialog width grows with the size of the screen, this property is
	 * useful on desktop when you might need different width sizes across your
	 * application.
	 * Set to `false` to disable `maxWidth`.
	 */
	maxWidth: PropTypes.oneOf(['xs', 'sm', 'md', 'lg', false]),
	// Determine the container for scrolling the dialog.
	scroll: PropTypes.oneOf(['body', 'paper']),
};

const baseStyles = {
	container: {
		height: '100%',
		outline: 'none',
	},
	paper: {
		position: 'relative',
		display: 'flex',
		flexDirection: 'column',
		overflowY: 'auto',
	},
	root: {},
};

const Dialog = forwardRef((props, ref) => {
	const [
		{ styles, classes },
		{
			children,
			className,
			disableBackdropClick,
			onBackdropClick,
			onClose,
			onEnd,
			onStart,
			open,
			PaperProps,
			TransitionComponent,
			TransitionProps,
			...passThru
		},
	] = useStyles(props, getStyles, { baseStyles });

	const handleBackdropClick = useCallback(event => {
		if (event.target !== event.currentTarget) return;
		if (onBackdropClick) onBackdropClick(event);
		if (!disableBackdropClick && onClose) onClose(event, 'backdropClick');
	}, []);

	return (
		<Modal
			className={className}
			disableBackdropClick={disableBackdropClick}
			onBackdropClick={handleBackdropClick}
			onClose={onClose}
			open={open}
			role="dialog"
			styles={styles.root}
			{...passThru}
		>
			<TransitionComponent
				className={classes.container}
				onEnd={onEnd}
				onStart={onStart}
				role="document"
				show={open}
				{...TransitionProps}
			>
				<Paper
					elevation={23}
					ref={ref}
					styles={styles.paper}
					{...PaperProps}
				>
					{children}
				</Paper>
			</TransitionComponent>
		</Modal>
	);
});

Dialog.displayName = 'Dialog';

Dialog.propTypes = {
	BackdropProps: PropTypes.object,
	//  Dialog children, usually the included sub-components.
	children: PropTypes.node.isRequired,
	className: PropTypes.string,
	// If `true`, clicking the backdrop will not fire the `onClose` callback.
	disableBackdropClick: PropTypes.bool,
	// If `true`, hitting escape will not fire the `onClose` callback.
	disableEscapeKeyDown: PropTypes.bool,
	// Callback fired when the backdrop is clicked.
	onBackdropClick: PropTypes.func,
	// Callback fired when the component requests to be closed.
	// @param {object} event The event source of the callback
	onClose: PropTypes.func,
	// Callback fired before the dialog enters.
	onEnter: PropTypes.func,
	// Callback fired when the dialog has entered.
	onEntered: PropTypes.func,
	// Callback fired when the dialog is entering.
	onEntering: PropTypes.func,
	// Callback fired when the escape key is pressed,
	// `disableKeyboard` is false and the modal is in focus.
	onEscapeKeyDown: PropTypes.func,
	// Callback fired before the dialog exits.
	onExit: PropTypes.func,
	// Callback fired when the dialog has exited.
	onExited: PropTypes.func,
	// Callback fired when the dialog is exiting.
	onExiting: PropTypes.func,
	// If `true`, the Dialog is open.
	open: PropTypes.bool.isRequired,
	// Properties applied to the [`Paper`](/api/paper/) element.
	PaperProps: PropTypes.object,
	...stylesPropType,
	// Transition component.
	TransitionComponent: PropTypes.oneOfType([
		PropTypes.string,
		PropTypes.func,
		PropTypes.object,
	]),
	// The duration for the transition.
	transitionDuration: durationPropType,
	// The easing for the transition.
	transitionEase: easingPropType,
	// Properties applied to the `Transition` element.
	TransitionProps: PropTypes.object,
	...getStyles.propTypes,
	...stylesPropType,
};

Dialog.defaultProps = {
	disableBackdropClick: false,
	disableEscapeKeyDown: false,
	fullScreen: false,
	fullWidth: false,
	maxWidth: 'sm',
	scroll: 'paper',
	styles: {},
	TransitionComponent: Fade,
};

export default Dialog;



